import React, {Component} from 'react';
import moment from 'moment';
import countdown from 'countdown';

//https://www.florin-pop.com/blog/2019/05/countdown-built-with-react/
class CountDown extends Component {
    state = {
        countdowntimer: undefined,
        years: undefined,
        months: undefined, 
        days: undefined,
        hours: undefined,
        minutes: undefined,
        seconds: undefined
    };

    componentDidMount() {
        this.interval = setInterval(() => {
            const { timeTillDate, timeFormat } = this.props;
            const countdowntimer = countdown(Date.now(), moment(timeTillDate, timeFormat));
            const years = countdowntimer.years;
            const months = countdowntimer.months;
            const days = countdowntimer.days;
            const hours = countdowntimer.hours;
            const minutes = countdowntimer.minutes;
            const seconds = countdowntimer.seconds;
            this.setState({ countdowntimer, years, months, days, hours, minutes, seconds });
        }, 1000);
    }

    componentWillUnmount() {
        if (this.interval) {
            clearInterval(this.interval);
        }
    }

    render() {
        const { years, months, days, hours, minutes, seconds } = this.state;
    
        // Mapping the date values to radius values
        const monthsRadius = mapNumber(months, 12, 0, 0, 360);
        const daysRadius = mapNumber(days, 30, 0, 0, 360);
        const hoursRadius = mapNumber(hours, 24, 0, 0, 360);
        const minutesRadius = mapNumber(minutes, 60, 0, 0, 360);
        const secondsRadius = mapNumber(seconds, 60, 0, 0, 360);

        if (!seconds) {
            return null;
        }

        return (
            <>
                <div className="countdown-wrapper">
                    {(years>=0) && (
                        <div className={"countdown-item "+ (this.props.showBorder ? 'border border-primary': '')}>
                            {years}
                            <span>years</span>
                        </div>
                    )}
                    {(months>=0) && (
                        <div className={"countdown-item "+ (this.props.showBorder ? 'border border-primary': '')}>
                            {this.props.showCircles && (
                                <SVGCircle radius={monthsRadius} />
                            )}
                            {months}
                            <span>months</span>
                        </div>
                    )}
                    {(days >= 0) && (
                        <div className={"countdown-item "+ (this.props.showBorder ? 'border border-primary': '')}>
                            {this.props.showCircles && (
                                <SVGCircle radius={daysRadius} />
                            )}
                            {days}
                            <span>days</span>
                        </div>
                    )}
                    {(hours >= 0) && (
                        <div className={"countdown-item "+ (this.props.showBorder ? 'border border-primary': '')}>
                            {this.props.showCircles && (
                                <SVGCircle radius={hoursRadius} />
                            )}
                            {hours}
                            <span>hours</span>
                        </div>
                    )}
                    {(minutes >= 0) && (
                        <div className={"countdown-item "+ (this.props.showBorder ? 'border border-primary': '')}>
                            {this.props.showCircles && (
                                <SVGCircle radius={minutesRadius} />
                            )}
                            {minutes}
                            <span>minutes</span>
                        </div>
                    )}
                    {(seconds >= 0) && (
                        <div className={"countdown-item "+ (this.props.showBorder ? 'border border-primary': '')}>
                            {this.props.showCircles && (
                                <SVGCircle radius={secondsRadius} />
                            )}
                            {seconds}
                            <span>seconds</span>
                        </div>
                    )}
                </div>
            </>
        );
    }
}

const SVGCircle = ({ radius }) => (
    <svg className="countdown-svg">
        <path
            fill="none"
            stroke="#333"
            strokeWidth="4"
            d={describeArc(50, 50, 48, 0, radius)}
        />
    </svg>
);

// From StackOverflow: https://stackoverflow.com/questions/5736398/how-to-calculate-the-svg-path-for-an-arc-of-a-circle
function polarToCartesian(centerX, centerY, radius, angleInDegrees) {
    var angleInRadians = ((angleInDegrees - 90) * Math.PI) / 180.0;

    return {
        x: centerX + radius * Math.cos(angleInRadians),
        y: centerY + radius * Math.sin(angleInRadians)
    };
}

function describeArc(x, y, radius, startAngle, endAngle) {
    var start = polarToCartesian(x, y, radius, endAngle);
    var end = polarToCartesian(x, y, radius, startAngle);

    var largeArcFlag = endAngle - startAngle <= 180 ? '0' : '1';

    var d = [
        'M',
        start.x,
        start.y,
        'A',
        radius,
        radius,
        0,
        largeArcFlag,
        0,
        end.x,
        end.y
    ].join(' ');

    return d;
}

// From StackOverflow: https://stackoverflow.com/questions/10756313/javascript-jquery-map-a-range-of-numbers-to-another-range-of-numbers
function mapNumber(number, in_min, in_max, out_min, out_max) {
    return (
        ((number - in_min) * (out_max - out_min)) / (in_max - in_min) + out_min
    );
}

export default CountDown;